
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'

const format = require("date-fns/format");


import echarts from "echarts/lib/echarts";
import "echarts/lib/chart/line";

import "echarts/lib/component/tooltip";
import "echarts/lib/component/title";
import "echarts/lib/component/legend";
export default {
components: {
Grid,
GridItem,
GroupTitle
},
data() {
return {
user: {},
allInfo: {},
allMoney: {},
col: 2,
date: [
format(new Date() - 6 * 24 * 3600 * 1000, "MM-DD"),
format(new Date() - 5 * 24 * 3600 * 1000, "MM-DD"),
format(new Date() - 4 * 24 * 3600 * 1000, "MM-DD"),
format(new Date() - 3 * 24 * 3600 * 1000, "MM-DD"),
format(new Date() - 2 * 24 * 3600 * 1000, "MM-DD"),
format(new Date() - 1 * 24 * 3600 * 1000, "MM-DD"),
format(new Date(), "MM-DD")
]
};
},
created() {
this.axios.get(this.api + "/wx/getUser").then(res => {
if (res.data.statu == 1) {

this.user = res.data.user;
} else {
window.location = this.api + "/wx/login?goback=businessman";
}
});
this.axios.get(this.api + "/wx/getAllHome").then(res => {
console.log(res);
this.allInfo = res.data;
});
this.axios.get(this.api + "/wx/getAllMoney").then(res => {
console.log(res);
this.allMoney = res.data;
});
},
methods: {
drawLine() {

let myChart = echarts.init(document.getElementById("myCharts"));

myChart.setOption({
tooltip: {
trigger: "axis"
},
legend: {
data: ["", "", ""]
},
textStyle: {
fontFamily: "Arial",
fontSize: "14px"
},
calculable: true,
xAxis: [
{
type: "category",
boundaryGap: false,
data: this.date
}
],
yAxis: [
{
type: "value"
}
],
series: [
{
name: "",
type: "line",
smooth: true,
stack: "",
data: [120, 132, 101, 134, 90, 230, 210],
label: { show: true }
},
{
name: "",
type: "line",
smooth: true,
stack: "",
data: [220, 182, 191, 234, 290, 330, 310],
label: { show: true }
},
{
name: "",
type: "line",
smooth: true,
stack: "",
data: [150, 232, 201, 154, 190, 330, 410],
label: { show: true }
}
]
});
}
},
mounted() {
this.drawLine();
}
};



